<template>
  <div id="app">
    <div class="drap-content">
      <div class="drap-before"
        @drop='drop($event)'
        @dragover='allowDrop($event)'>
        <div
          @click="clickit"
          :draggable="isDrag"
          @dragstart="drag($event)"
          :style="[this.isActive ? this.active : '', this.common]">
          {{ fruit }}
        </div>
      </div>
      <div class="drap-after"
        @drop='drop($event)'
        @dragover='allowDrop($event)'>
      </div>
      <div class="seePos">
        <p>我的X坐标是: {{ isPos[0].x }}</p>
        <p>我的Y坐标是: {{ isPos[0].y }}</p>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'app',
  data () {
    return {
      fruit: '臭豆腐',
      isDrag: false,
      isActive: false,
      dom: null,
      timer: null,
      isPos: [{x: '你猜', y: '你猜'}],
      common: {
        width: '100px',
        height: '100px',
        lineHeight: '100px',
        border: '1px solid',
        borderRadius: '50%',
        textAlign: 'center',
        cursor: 'pointer',
        margin: '25px auto'
      },
      active: {
        color: '#F0F'
      }
    }
  },
  methods: {
    clickit: function (event) {
      this.isDrag = !this.isDrag
      this.isActive = !this.isActive
      this.isActive ? this.fruit = '榴莲' : this.fruit = '臭豆腐'
      this.isPos[0].x = event.screenX
      this.isPos[0].y = event.screenY
    },
    drag: function (event) {
      this.dom = event.currentTarget
      this.isPos[0].x = event.screenX
      this.isPos[0].y = event.screenY
    },
    drop: function (event) {
      event.preventDefault()
      event.target.appendChild(this.dom)
      this.isPos[0].x = event.screenX
      this.isPos[0].y = event.screenY
    },
    allowDrop: function (event) {
      event.preventDefault()
      this.isPos[0].x = event.screenX
      this.isPos[0].y = event.screenY
    }
  },
  watch: {
    isPos: {
      handler: function (val, oldVal) {
        console.log(val[0].x)
        console.log(val[0].y)
      },
      deep: true
    }
  }
}
</script>

<style>
  .drap-before,.drap-after {
    width: 300px;
    height: 150px;
    border: 1px solid;
    box-sizing: border-box;
    text-align: center;
    margin: 0 auto;
  }
  .drap-after {
    margin-top: 100px;
  }
  .seePos {
    margin: 0 auto;
    width: 300px;
    height: 300px;
    display: table;
    text-align: center;
  }
  .seePos > p {
    display: table-cell;
    vertical-align: middle;
  }
</style>



